<template>
  <div id="app">
    <router-view></router-view>
    <van-tabbar v-model="active" @change="changeRoute">
      <van-tabbar-item v-for="(item, index) in icons" :key="index">
        <span :style="{color:active==index?'rgb(235, 97, 0)':''}">{{ item.title }}</span>
        <template #icon="props">
          <img :src="props.active ? icons[index].active : icons[index].inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
export default {
  // 组件名称
  name: "",
  // 组件参数 接收来自父组件的数据
  props: [],
  // 局部注册的组件
  components: {},
  // 组件状态值
  data() {
    return {
      active: 0,
      icons: [
        {
          title: "首页",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,"
        },

        {
          title: "课程",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,"
        },

        {
          title: "约课记录",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,"
        },

        {
          title: "练习",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,"
        },

        {
          title: "我的",
          active: "https://wap.365msmk.com/img/home-active.87af5fd4.png",
          inactive:
            "data:image/png;base64,"
        }
      ]
    };
  },
  // 计算属性
  computed: {},
  // 侦听器
  watch: {},
  // 组件方法
  updated() {},
  methods: {
    changeRoute() {
      switch (this.active) {
        case 0:
          this.$router.push({ path: "/" });
          break;
        case 1:
          this.$router.push({ path: "/Appointment" });
          break;
        case 2:
          this.$router.push({ path: "/order" });
          break;
        case 3:
          this.$router.push({ path: "/practice" });
          break;
        case 4:
          // if (sessionStorage.getItem("token")) {
          this.$router.push({ path: "/mine" });
          // }else{
          // this.$router.push({ path: "/login" });
          // }
          break;
      }
    },
    changeRouter(index) {
      this.active = index;
    }
  },
  /**
   * 组件实例创建完成，属性已绑定，但DOM还未生成，$ el属性还不存在
   */
  created() {},
  mounted() {
    var Scr = String(window.location.href.split("#")[1]);
    console.log(Scr)
    if (Scr == "/mine") {
      this.changeRouter(4);
    }
    if (Scr == "/practice") {
      this.changeRouter(3);
    }
    if (Scr == "/order") {
      this.changeRouter(2);
    }
    if (Scr == "/Appointment") {
      this.changeRouter(1);
    }
    if (Scr == "/home") {
      this.changeRouter(0);
    }
  }
};
</script> 

<style scoped>
.tabBar {
  width: 100%;
  height: 0.6rem;
}
</style>
